@keyframes wheelRotation {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@keyframes movement {
  0% { left: 0%; opacity: 0; }
  10% { opacity: 1;}
  80% { opacity: 1;}
  100% { left: 95%; opacity: 0; }
}

